import React from 'react';
import ReactDOM from 'react-dom';
import createReactClass from 'create-react-class';
import './App.css';

var App = createReactClass({

	//Set initial state and starting index for new additions
    getInitialState: function() {
        return {
            hw_list: '',
			nextIndex: 11
        };
    },
    
    componentDidMount() {    
		var that = this;
		var url = 'data.json';

		fetch(url)
			.then(function(response) {
				if (response.status >= 400) {
					throw new Error("Error " + response.status);
				}
				return response.json();
			})
			.then(function(data) {
				
				//Parse data into an array for processing
				var outer_array = [];
				for (var i=0; i<data.hardware.length; i++)
				{
					outer_array.push(data.hardware[i].id + 
							 "," + data.hardware[i].name + 
							 "," + data.hardware[i].model + 
							 "," + data.hardware[i].address);
				}

				that.setState({
					hw_list:outer_array
				});
			});
    },

	//Function to remove an item from the list based off of id #.
    removeItem: function(target_id) {
		
		var outer_array = [];
		var temp_row = [];
		
		//Rebuild array, skipping unwanted row
		for (var i=0; i<this.state.hw_list.length; i++)
		{
			temp_row = this.state.hw_list[i].split(",");
			if (temp_row[0] !== target_id) {
				outer_array.push(temp_row[0] + 
						 "," + temp_row[1] + 
						 "," + temp_row[2] + 
						 "," + temp_row[3]);
			}
		}
		this.setState({
			hw_list: outer_array
		});
    },
	
	//Function to add a new row. Simply push new row to the end.
    addItem: function(newrow) {
		var outer_array = this.state.hw_list;
		outer_array.push(this.state.nextIndex + "," 
			+ newrow.nametxt + "," 
			+ newrow.modeltxt + "," 
			+ newrow.addresstxt);
		this.setState({
		   hw_list: outer_array,
		   nextIndex: this.state.nextIndex + 1
		});
    },

	//Render the table itself.
    render: function() {
		var that = this;
		
        return (
			<div className="div-table">
				<div className="div-tr">
					<div className="div-th">
						ID
					</div>
					<div className="div-th">
						Name
					</div>
					<div className="div-th">
						Model
					</div>
					<div className="div-th">
						MAC Address
					</div>
					<div className="div-th">
						 
				</div>
			</div>
			
			{
				Object.values(this.state.hw_list).map(function (hw_list){
					var temprow = hw_list.split(",");
					var row_id = temprow[0];
					var row_name = temprow[1];
					var row_model = temprow[2];
					var row_address = temprow[3];
					
					return (
						<HardwareRow  key={row_id} row_name={row_name} 
							row_id={row_id} row_model={row_model} 
							row_address={row_address} removeItem={that.removeItem} />
					)
				})
			}
			<NewRow addItem={that.addItem} array={this.state.hw_list} index={this.state.nextIndex} />
			</div>
        )
    }
});

//To dynamically render rows via map.
class HardwareRow extends React.Component{
    render() {
		return (
			<div className="div-tr">
				<div className="div-td">
					{this.props.row_id}
				</div>
				<div className="div-td">
					{this.props.row_name}
				</div>
				<div className="div-td">
					{this.props.row_model}
				</div>
				<div className="div-td">
					{this.props.row_address}
				</div>
				<div className="div-td">
				<button onClick={() => this.props.removeItem(this.props.row_id)}>
					Remove item #{this.props.row_id}
				</button>
				</div>
			</div>
		)
    }
}

//To create the new row object that listens for input.
class NewRow extends React.Component{
    
	//Default stuff.
	constructor(props) {
		super (props);
		this.state={
            nametxt: '',
			modeltxt: '',
			addresstxt: ''
		}
		
		this.setName = this.setName.bind(this);
		this.setModel = this.setModel.bind(this);
		this.setAddress = this.setAddress.bind(this);
	}
	
	//Sets name.
	setName(text) {
		this.setState({
			nametxt: text.target.value,
		});
	}
	
	//Sets model.
	setModel(text) {
		this.setState({
			modeltxt: text.target.value,
		});
	}
	
	//Sets MAC address.
	setAddress(text) {
		this.setState({
			addresstxt: text.target.value,
		});
	}
				
	//Render new row, "Add Item" button.
    render() {
		return (
			<div className="div-tr">
				<div className="div-td">
					{this.props.index}
				</div>
				<div className="div-td">
					<input type="text" value={this.state.nametxt} onChange={this.setName}/>
				</div>
				<div className="div-td">
					<input type="text" value={this.state.modeltxt} onChange={this.setModel}/>
				</div>
				<div className="div-td">
					<input type="text" value={this.state.addresstxt} onChange={this.setAddress}/>
				</div>
				<div className="div-td">
					<button onClick={() => this.props.addItem(this.state)}>Add Item</button>
				</div>
			</div>
		)
    }
}

ReactDOM.render(<App/>, document.body);
export default App;



